.Listlocat{
    display: flex;
    
    .homecontainer{
       
        flex:6;
        background-color:whitesmoke;
        .actionbot{
            display: flex;
            
            width: 850px;
            height: 70px;
           
            justify-content: flex-end;
            align-items: flex-start;
            margin-right: 3%;
            margin-top: 10px;
        .addbot{
        
           
            width: 130px;
            height: 60px;
            color:white;
            border-radius: 30px;
            background-color: rgb(37, 129, 190); 
            transition: 0.5s;
            
            &:hover{
                background-color: rgb(225, 219, 219);
                color:rgb(37, 129, 190);  
                scale: 1.1;
                
            }
        
    }}
    }
}